import { type PuzzleType, type PuzzleInputProps } from "~/types/puzzle";
import { InputPuzzle } from "./puzzles/input-puzzle";
import { MultipleChoicePuzzle } from "./puzzles/multiple-choice-puzzle";
import { QRCodePuzzle } from "./puzzles/qrcode-puzzle";
import { ImagePuzzle } from "./puzzles/image-puzzle";
import { LocationPuzzle } from "./puzzles/location-puzzle";

export function PuzzleInput({ type, choices = [], onSubmit, disabled, locationData, imageUrl }: PuzzleInputProps) {
    switch (type) {
        case PuzzleType.MULTIPLE_CHOICE:
            return (
                <MultipleChoicePuzzle
                    choices={choices}
                    onSubmit={onSubmit}
                    disabled={disabled}
                />
            );
        case PuzzleType.QRCODE:
            return (
                <QRCodePuzzle
                    onSubmit={onSubmit}
                    disabled={disabled}
                />
            );
        case PuzzleType.IMAGE:
            if (!imageUrl) return <InputPuzzle onSubmit={onSubmit} disabled={disabled} />;
            return (
                <ImagePuzzle
                    imageUrl={imageUrl}
                    onSubmit={onSubmit}
                    disabled={disabled}
                />
            );
        case PuzzleType.LOCATION:
            if (!locationData) return <InputPuzzle onSubmit={onSubmit} disabled={disabled} />;
            return (
                <LocationPuzzle
                    locationData={locationData}
                    onSubmit={onSubmit}
                    disabled={disabled}
                />
            );
        case PuzzleType.INPUT:
        default:
            return <InputPuzzle onSubmit={onSubmit} disabled={disabled} />;
    }
} 