"use client";

import { useEffect, useState, use } from "react";
import { useRouter } from "next/navigation";
import { Card, CardContent, CardHeader, CardTitle } from "~/components/ui/card";
import { useToast } from "~/hooks/use-toast";
import { Loading } from "~/components/ui/loading";
import { ProgressBar } from "~/components/game/progress-bar";
import { PuzzleInput, type PuzzleType } from "~/components/game/puzzle-input";
import { useGameSession } from "~/hooks/use-game-session";

interface PuzzleData {
    id: number;
    title: string;
    description: string;
    order: number;
    type: PuzzleType;
    choices?: {
        id: string;
        text: string;
    }[];
}

interface TeamData {
    id: number;
    name: string;
}

interface GameInstance {
    id: number;
    currentPuzzleOrder: number;
}

interface ApiResponse {
    team: TeamData;
    game: {
        puzzles: PuzzleData[];
    };
    instance: GameInstance;
}

interface AnswerResponse {
    success: boolean;
    message: string;
    nextPuzzle?: {
        id: number;
    };
}

interface Props {
    params: Promise<{
        code: string;
        puzzleId: string;
    }>;
}

export default function PuzzlePage({ params }: Props) {
    const resolvedParams = use(params);
    const [puzzle, setPuzzle] = useState<PuzzleData | null>(null);
    const [team, setTeam] = useState<TeamData | null>(null);
    const [instance, setInstance] = useState<GameInstance | null>(null);
    const [submitting, setSubmitting] = useState(false);
    const [loading, setLoading] = useState(true);
    const router = useRouter();
    const { toast } = useToast();
    const { session, updateSession } = useGameSession(resolvedParams.code);

    useEffect(() => {
        async function fetchData() {
            try {
                const response = await fetch(`/api/bookings/${resolvedParams.code}`);
                if (!response.ok) throw new Error("Failed to fetch data");

                const data = (await response.json()) as ApiResponse;
                const currentPuzzle = data.game.puzzles.find(p => p.id === parseInt(resolvedParams.puzzleId));

                if (!currentPuzzle) {
                    throw new Error("Puzzle not found");
                }

                setPuzzle(currentPuzzle);
                setTeam(data.team);
                setInstance(data.instance);
                updateSession({ currentPuzzleId: currentPuzzle.id });
            } catch (error) {
                console.error("Error fetching data:", error);
                router.push("/");
            } finally {
                setLoading(false);
            }
        }

        void fetchData();
    }, [resolvedParams.code, resolvedParams.puzzleId, router]);

    async function handleSubmit(answer: string) {
        if (submitting) return;

        setSubmitting(true);
        try {
            const response = await fetch(`/api/bookings/${resolvedParams.code}/answer`, {
                method: "POST",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify({
                    puzzleId: parseInt(resolvedParams.puzzleId),
                    answer: answer.trim()
                }),
            });

            const data = (await response.json()) as AnswerResponse;

            if (data.success) {
                toast({
                    title: "Correct!",
                    description: data.message,
                });

                // If there's no next puzzle, go to completion page
                if (!data.nextPuzzle) {
                    router.push(`/play/${resolvedParams.code}/complete`);
                    return;
                }

                // Otherwise, go to next puzzle
                router.push(`/play/${resolvedParams.code}/puzzle/${data.nextPuzzle.id}`);
            } else {
                toast({
                    variant: "destructive",
                    title: "Incorrect",
                    description: data.message,
                });
            }
        } catch (error) {
            console.error("Error submitting answer:", error);
            toast({
                variant: "destructive",
                title: "Error",
                description: "Failed to submit answer",
            });
        } finally {
            setSubmitting(false);
        }
    }

    if (loading || !puzzle || !team || !instance) {
        return <Loading message="Loading puzzle..." />;
    }

    const totalPuzzles = puzzle.order; // Assuming puzzles are 1-indexed

    return (
        <div className="container max-w-2xl py-8 space-y-6">
            <ProgressBar
                currentPuzzle={instance.currentPuzzleOrder}
                totalPuzzles={totalPuzzles}
            />

            <Card className="border-slate-800 bg-slate-900/50">
                <CardHeader>
                    <CardTitle className="text-2xl">
                        Puzzle #{puzzle.order}: {puzzle.title}
                    </CardTitle>
                </CardHeader>
                <CardContent className="space-y-6">
                    <div className="prose prose-invert max-w-none">
                        <div dangerouslySetInnerHTML={{ __html: puzzle.description }} />
                    </div>

                    <PuzzleInput
                        type={puzzle.type}
                        choices={puzzle.choices}
                        onSubmit={handleSubmit}
                        disabled={submitting}
                    />
                </CardContent>
            </Card>
        </div>
    );
} 