import { useState } from "react";
import { Button } from "~/components/ui/button";
import { Input } from "~/components/ui/input";
import { type LocationPuzzleProps } from "~/types/puzzle";
import { Card, CardContent, CardHeader, CardTitle } from "~/components/ui/card";

export function LocationPuzzle({ locationData, onSubmit, disabled }: LocationPuzzleProps) {
    const [answer, setAnswer] = useState("");
    const [location, setLocation] = useState<GeolocationPosition | null>(null);
    const [error, setError] = useState<string | null>(null);

    const handleGetLocation = () => {
        if (!navigator.geolocation) {
            setError("Geolocation is not supported by your browser");
            return;
        }

        navigator.geolocation.getCurrentPosition(
            (position) => {
                setLocation(position);
                setError(null);
            },
            (error) => {
                setError(error.message);
                setLocation(null);
            }
        );
    };

    const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
        e.preventDefault();
        if (!answer.trim() || disabled) return;

        // If we have location data, include it in the answer
        const finalAnswer = location
            ? `${answer.trim()}|${location.coords.latitude},${location.coords.longitude}`
            : answer.trim();

        onSubmit(finalAnswer);
        setAnswer("");
    };

    return (
        <div className="space-y-4">
            <Card>
                <CardHeader>
                    <CardTitle>Location Information</CardTitle>
                </CardHeader>
                <CardContent>
                    <p className="text-sm text-muted-foreground">{locationData}</p>
                </CardContent>
            </Card>

            <div className="space-y-2">
                <Button
                    type="button"
                    variant="secondary"
                    className="w-full"
                    onClick={handleGetLocation}
                    disabled={disabled}
                >
                    {location ? "Update Location" : "Get Current Location"}
                </Button>

                {error && (
                    <p className="text-sm text-destructive">{error}</p>
                )}

                {location && (
                    <p className="text-sm text-muted-foreground">
                        Location captured: {location.coords.latitude.toFixed(6)},{" "}
                        {location.coords.longitude.toFixed(6)}
                    </p>
                )}
            </div>

            <form onSubmit={handleSubmit} className="space-y-4">
                <Input
                    type="text"
                    placeholder="Enter your answer"
                    value={answer}
                    onChange={(e) => setAnswer(e.target.value)}
                    disabled={disabled}
                    required
                />
                <Button
                    type="submit"
                    className="w-full"
                    disabled={disabled ?? !answer.trim()}
                >
                    {disabled ? "Checking..." : "Submit Answer"}
                </Button>
            </form>
        </div>
    );
} 