import { useState } from "react";
import { Button } from "~/components/ui/button";
import { RadioGroup, RadioGroupItem } from "~/components/ui/radio-group";
import { Label } from "~/components/ui/label";
import { type MultipleChoicePuzzleProps } from "~/types/puzzle";

export function MultipleChoicePuzzle({ choices, onSubmit, disabled }: MultipleChoicePuzzleProps) {
    const [answer, setAnswer] = useState("");

    const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
        e.preventDefault();
        if (!answer || disabled) return;

        onSubmit(answer);
        setAnswer("");
    };

    return (
        <form onSubmit={handleSubmit} className="space-y-4">
            <RadioGroup
                value={answer}
                onValueChange={setAnswer}
                disabled={disabled}
                required
            >
                {choices.map((choice) => (
                    <div key={choice.id} className="flex items-center space-x-2">
                        <RadioGroupItem value={choice.id} id={choice.id} />
                        <Label htmlFor={choice.id}>{choice.text}</Label>
                    </div>
                ))}
            </RadioGroup>

            <Button
                type="submit"
                className="w-full"
                disabled={disabled ?? !answer}
            >
                {disabled ? "Checking..." : "Submit Answer"}
            </Button>
        </form>
    );
} 